<template>
    <born-obstetrical-history-form />

    <born-oocyte-provider-form />
</template>

<script>
import { onMounted, onUnmounted, provide, watch } from "vue";
import { useBornCycleMonitoringInit } from "@/composable/born";

import BornObstetricalHistoryForm from "@/components/born/obstetrical-history-form.vue";
import BornOocyteProviderForm from "@/components/born/oocyte-provider-form.vue";
import { emitter } from "@/event-bus";
export default {
    components: {
        BornObstetricalHistoryForm,
        BornOocyteProviderForm
    },
    setup() {
        const {
            form,
            save,
            obstetricalHistory,
            fillForm
        } = useBornCycleMonitoringInit();

        watch(obstetricalHistory, fillForm);

        onMounted(() => {
            if (obstetricalHistory.value) {
                fillForm(obstetricalHistory.value);
            }
            emitter.on("oocyte-provider-form:save", save);
        });

        onUnmounted(() => {
            emitter.off("oocyte-provider-form:save", save);
        });

        provide("form", form);
    }
};
</script>
